import React, {Component} from 'react';
import './ChatDoor.css';
import centerImg from './assets/img.png'

const text = {
    enterChatRoom: '들어가기',
}

class ChatDoor extends Component {
    constructor() {
        super()
        this.state = {
            roomName: '',
            roomUrl: '',
            roomPassword: ''
        }
    }

    render() {
        const {roomUrl} = this.state
        return (
            <div className={'chatdoor-page'}>
                <div className={'title-wrapper'}>
                    <span>'노원역 1번출구 버스킹'</span>
                </div>

                <div className={'img-wrapper'}>
                <img src={centerImg}/>
                </div>

                <button className={'bottom-btn'}>
                    <span>{text.enterChatRoom}</span>
                </button>

            </div>
        )
    }
}

export default ChatDoor;



